import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 0,
    todoText: "Walk the dog",
    todoComplete: true
  },
  {
    todoID: 0,
    todoText: "Clean my room",
    todoComplete: false
  }
];

const todoList = document.querySelector(".todoList");
const add = document.querySelector(".add");
const clear = document.querySelector(".clear");

for (todos in todos) {
  let listItem = document.createElement("li");
  todoList.appendChild(listItem);
  let listItemText = todos.todoText;
  listItem.textContent = listItemText;
}

add.addEventListener("click", () => {
  const input = document.querySelector(".input").value;
  let newTask = [{ todoID: 0, todoText: input, todoComplete: false }];
  console.log(newTask);
  todos.push(newTask);
  console.log(todos);
});

clear.addEventListener("click", () => {
  todos.pop();
  console.log(todos);
});
